import React from "react";

import DOMPurify from "dompurify";
import ReactHtmlParser from "react-html-parser";
import Avatar from "react-avatar";

import styles from "./post.module.css";
import { Row, Col } from "antd";

const Post = ({ profileImage, name, content, date, time }) => {
  const clean = DOMPurify.sanitize(content);

  return (
    <div className="panel panel-default p-3">
      <div className="panel-body">
        <Row gutter={24}>
          <Col span={6}>
            <Avatar
              className="float-right"
              size={50}
              name={name}
              src={profileImage}
            />
          </Col>
          <Col span={18}>
            <p
              className={`mb-1 ${styles.name}`}
              style={{ textAlign: "end", verticalAlign: "bottom" }}
            >
              {name}
            </p>
          </Col>
        </Row>
        <Row gutter={24}>
          <Col span={24}>
            {/* <div
              // className="description__detaillEvenement"
              dangerouslySetInnerHTML={{
                __html: clean,
              }}
            /> */}
            <div className="comment_detail">{ReactHtmlParser(clean)}</div>
          </Col>
        </Row>
        <Row gutter={24}>
          <Col span={12}>
            <p className={`mb-0 text-right ${styles.dateTime}`}>{date}</p>
          </Col>
          <Col span={12}>
            <p
              className={`mb-0 text-left ${styles.dateTime}`}
              style={{ textAlign: "end" }}
            >
              {time}
            </p>
          </Col>
        </Row>
      </div>
    </div>
  );
};

export default Post;
