import React, { useState, useEffect } from "react";

import PropTypes from "prop-types";
import ReactPaginate from "react-paginate";

import Post from "./components/Post";
import PostEditor from "./components/PostEditor";

import styles from "./index.module.css";
import moment from "moment";
import "moment/locale/fr";

const DiscussionBoard = ({
  onSubmit,
  posts,
  addText,
  nextText,
  prevText,
  submitText,
}) => {
  const [newPost, setNewPost] = useState(false);
  const [text, setText] = useState("");

  const perPage = 3;
  const [pageCount, setPageCount] = useState(0);
  const [pagePosts, setPagePosts] = useState([]);
  const [currentPage, setCurrentPage] = useState(0);

  useEffect(() => {
    setPageCount(Math.ceil(posts.length / perPage));

    if (posts.length % perPage !== 0 && posts.length > perPage) {
      setPagePosts(
        posts.slice(
          posts.length - (posts.length % perPage) - 1,
          posts.length - 1
        )
      );

      setCurrentPage(pageCount - 1);
    } else if (posts.length % perPage === 0 && posts.length > perPage) {
      setPagePosts(posts.slice(posts.length - perPage, posts.length));
      setCurrentPage(pageCount);
    } else {
      setPagePosts(posts.slice(0, perPage));
      setCurrentPage(0);
    }

    return () => {
      setPagePosts([]);
    };
  }, [posts]);

  const clickNewPost = () => {
    setNewPost(true);
  };

  const buildDate = (date) => {
    moment.locale("fr");
    return moment(date).format("L");
  };

  const addZero = (i) => {
    if (i < 10) {
      i = "0" + i;
    }
    return i;
  };

  const buildTime = (date) => {
    let hours = date.getHours();
    return addZero(hours) + ":" + addZero(date.getMinutes());
  };

  const submitPost = () => {
    onSubmit(text);
    setNewPost(false);
    setText("");
  };

  const onPageChange = ({ selected }) => {
    let offset = Math.ceil(selected * perPage);
    setPagePosts(posts.slice(offset, offset + perPage));
    setCurrentPage(selected);
  };
  console.log("texxxxt", text);
  const displayEditor = () => {
    return (
      <React.Fragment>
        <div className="row">
          <div className="col">
            <PostEditor text={text} setText={setText} />
          </div>
        </div>
        <div className="row pt-2">
          <div className="col">
            <button
              onClick={submitPost}
              className={text === "" ? "submit-btn-disabled" : "submit-btn"}
              disabled={text === ""}
            >
              {submitText}
            </button>
          </div>
        </div>
      </React.Fragment>
    );
  };

  return (
    <div className="container">
      <a
        onClick={clickNewPost}
        className={`pl-3 text-decoration-none ${styles.newPost}`}
      >
        {addText}
      </a>
      {pagePosts.length > 0 ? (
        <>
          {pagePosts.map((post, idx) => {
            const newDate = buildDate(post.date);
            const newTime = buildTime(post.date);

            return (
              <React.Fragment key={idx}>
                <Post {...post} date={newDate} time={newTime} />
              </React.Fragment>
            );
          })}
        </>
      ) : (
        <>
          <div className="panel panel-default p-3"></div>
        </>
      )}

      {newPost ? displayEditor() : ""}

      <div className="d-flex justify-content-center mt-5">
        <ReactPaginate
          pageCount={pageCount}
          pageRangeDisplayed={5}
          marginPagesDisplayed={5}
          previousLabel={prevText}
          nextLabel={nextText}
          breakLabel="..."
          breakClassName="page-item"
          onPageChange={onPageChange}
          forcePage={currentPage}
          containerClassName="pagination"
          pageClassName="page-item-page"
          pageLinkClassName="page-link"
          previousClassName="page-item"
          nextClassName="page-item"
          previousLinkClassName="page-link"
          nextLinkClassName="page-link"
        />
      </div>
    </div>
  );
};

DiscussionBoard.defaultProps = {
  posts: [],
};

DiscussionBoard.propTypes = {
  posts: PropTypes.array,
  onSubmit: PropTypes.func,
};

export default DiscussionBoard;
